.offers {
    top: 0;
    left: 0;
    padding-bottom: 80px;
    background: linear-gradient(var(--color-accent-3) 50%, var(--color-white) 50%);
    &__title {
        text-align: center;
        font-size: 48px;
        color: var(--color-accent-1);
        padding: 50px 0;
    }
    &__list {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__item {
        width: 20%;
        &:not(:last-child) {
            padding-right: 15px;
        }
        &__name {
            background-image: url("../img/hero.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            height: 200px;
        }
        &__category {
            font-weight: 400;
            display: inline-block;
            font-size: 16px;
            background-color: var(--color-accent-1);
            padding: 10px;
            margin: 25px 0 0 25px;
            color: var(--color-white);
        }
        &__link {
            display: flex;
            align-items: center;
            height: 80px;
            background-color: var(--color-accent-1);
            margin: 0;
            color: var(--color-white);
            padding: 0 10px 0 10px;
        }
        &__descr {
            line-height: 1.5;
            font-size: 18px;
        }
    }
}