.news {
    top: 0;
    left: 0;
    padding-bottom: 80px;
    &__container {
        display: flex;
        justify-content: space-between;
    }
    &__left {
        background-color: var(--color-accent-2);
        color: var(--color-white);
        max-width: 42%;
    }
    &__title {
        font-weight: 400;
        display: inline-block;
        font-size: 18px;
        background-color: var(--color-accent-1);
        padding: 10px;
        margin: 40px 0 30px 40px;
        color: var(--color-white);
    }
    &__text {
        font-size: 16px;
        line-height: 1.5;
        margin: 0 0 30px 40px;
        max-width: 75%;
        padding: 20px 0;
    }
    &__text-container--active {
        background-color: var(--color-accent-1);
    }
    &__right {
        max-width: 58%;
        background-color: var(--color-accent-3);
    }
    &__right__title {
        font-weight: 600;
        color: var(--color-black);
        font-size: 30px;
        padding: 40px 40px 20px 40px;
        margin: 0;
    }
    &__list {
        padding: 20px 40px 20px 40px;
    }
    &__item {
        font-size: 16px;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    &__item__circle {
        fill: var(--color-accent-1);
        margin-bottom: 18px;
        margin-right: 10px;
    }
    &__item:not(:last-child) {
        padding-bottom: 30px;
    }
}